import React from 'react';
import { Link } from 'react-router-dom';
import Bookcase from './Bookcase';
import smile from '../smile.svg';

const MainPage = props => {
  return (
    <React.Fragment>
      <header id="main-header" role="banner">
        <h1>Book Shelves</h1>
      </header> {/* header */}

      <main role="main" style={{backgroundColor: props.colors.bookcase ? props.colors.bookcase : '#BA976E', borderColor: props.colors.frames ? props.colors.frames : '#5d4c37'}}>
        <Bookcase 
          categorizedBooks={props.categorizedBooks}
          removeBook={props.removeBook}
          changeCategory={props.changeCategory} 
          colors={props.colors}
        />
        <Link 
          id="add-link"
          to="/add"
          style={{color: (props.colors.interactiveText ? props.colors.interactiveText : '#3a375d'), backgroundColor: (props.colors.interactiveTextBackground ? props.colors.interactiveTextBackground : '#c7c5e3')}}
        >
          Add
        </Link>
      </main> {/* Main */}

      <form id="colors-form">
        <h1>Good luck <img src={smile}/></h1>
        <p>
          <label htmlFor="bookcase">bookcase: </label>
          <input value={props.colors.bookcase} type="text" id="bookcase" style={{color: props.colors.bookcase ? props.colors.bookcase : '#BA976E'}} onChange={event => {
            props.setColors('bookcase', event.target.value)
          }}/>
        </p>
        <p>
          <label htmlFor="frames">frames: </label>
          <input value={props.colors.frames} type="text" id="frames" style={{color: props.colors.frames ? props.colors.frames : '#5d4c37'}} onChange={event => {
            props.setColors('frames', event.target.value)
          }}/>
        </p>
        <p>
          <label htmlFor="text">text: </label>
          <input value={props.colors.text} type="text" id="text" style={{color: props.colors.text ? props.colors.text : '#382d21'}} onChange={event => {
            props.setColors('text', event.target.value)
          }}/>
        </p>
        <p>
         <label htmlFor="interactiveText">text2: </label>
          <input value={props.colors.interactiveText} type="text" id="interactiveText" style={{color: props.colors.interactiveText ? props.colors.interactiveText :  '#3a375d'}} onChange={event => {
            props.setColors('interactiveText', event.target.value)
          }}/>
        </p>
        <p>
          <label htmlFor="interactiveTextBackground">text2 back: </label>
          <input value={props.colors.interactiveTextBackground} type="text" id="interactiveTextBackground" style={{color: props.colors.interactiveTextBackground ?   props.colors.interactiveTextBackground : '#c7c5e3'}} onChange={event => {
            props.setColors('interactiveTextBackground', event.target.value)
          }}/>
        </p>
      </form>
    </React.Fragment>
  );
};

export default MainPage;